import React, { Component } from 'react'

class Skills extends Component {
    constructor(props)
    {
        super(props);
        this.state={
            'myskills':['java','html','css','javascript','sql','react.js','angular','node.js', 'handlebars',
        'jenkins', 'jira', 'git', 'gradle', 'apache tomcat', 'npm', 'bitbucket', 'spring applications',
        'jpa/hibernate', 'ajax', 'soapui', 'rest api', 'docker', 'kubernetes', 'ios/android development',
         'web development', 'microsoft/google office suite']
        };
    }

    render() {
        return (
            <div className="condiv">
            <h1 className="subtopic">My Skills</h1>
            <div>Programming Languages/technologies that I'm familiar with! 
                These are just self estimates of my knowledge, 
                but I am always looking to learn more and keep up with the current market.</div>
            <ul>
            {this.state.myskills.map((value)=>{
                let upperCaseVal = value.toUpperCase();
                let skillsClass = "skills " + value;
                return (<li key={upperCaseVal}><p>{upperCaseVal}</p>
                        <div className="skillsContainer">
                            
                            <div className={skillsClass}></div>
                        </div>
                    </li>);
            })}
            </ul>
            </div>
            )
        }
    }
    
    export default Skills
    